/* CSS RESET BY DIGITAL OCEAN */
html {
  box-sizing: border-box;
  font-size: 16px;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
/* CSS RESET BY DIGITAL OCEAN */

/* CSS Variables */

:root {
  --dark-grey: rgb(54, 54, 54);
  --light-grey: rgb(119, 119, 119);
  --black: rgb(29, 29, 29);
  --darker-black: rgb(0, 0, 0);
}

/* CSS Variables */

body {
  background-color: rgb(29, 29, 29);
  height: 100vh;
  font-family:  'Source Code Pro', Arial, Helvetica, sans-serif;
  color: white;
}

/* .content-wrapper Section */
.content-wrapper {
  height: 100vh;
  display: flex;
}

/* .content-wrapper > */

/* .sidebar Section */
.sidebar {
  background-color: var(--darker-black);
  flex-shrink: 0;
  flex-basis: 15rem;
  padding-top: 1rem;
  padding: 1rem;
}

.sidebar > ul {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.sidebar > * {
  cursor: pointer;
}

li {
  padding-left: 15px;
}

.project-nav-items {
  margin-top: 15px;
  display: flex;
}

/* .nav-items Section */
.nav-items {
  font-size: 1.5rem;
}

/* .content Section */
.content {
  width: 100vw;
  background-color: var(--dark-grey);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-areas: "display-card";
  gap: 2rem;
}

/* .display-card Section */
.display-card {
  grid-area: display-card;
  margin-top: 14vh;
  margin-bottom: 4vh;
  margin-left: 1rem;
  margin-right: 16px;
  min-width: 20.8rem;
  max-width: 34rem;
  background-color: var(--light-grey);
  border-radius: .6rem;

  display: flex;
  flex-direction: column;

  box-shadow: 3px 3px 15px black;
}

/* .display-tasks Section */
.display-tasks {
  flex: 1 1 0;
  display: flex;
  flex-direction: column;
  background-color: var(--black);
  border-radius: .6rem .6rem 0 0;

  overflow-y: scroll;
  scrollbar-color: white var(--black) ;
}

/* .seperation-box Section */
.seperation-box {
  height: 1rem;
  background-color: var(--dark-grey);
}

/* .add-tasks Section */
.add-tasks {
  border: none;
  padding: .625rem;
  border-radius: 0 0 .6rem .6rem;
  cursor: pointer;
}

.task-checkbox-input {
  border: 1px solid #fff;
  padding: 0.5em;
  -webkit-appearance: none;
  appearance: none;
}

.task-checkbox-input:checked {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center;
  background-size: 9px 9px;
}

.task-checkbox-input:focus {
  outline-color: transparent;
}

.task-due-date-input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  box-sizing: border-box;
}
.task-due-date-input::-webkit-calendar-picker-indicator {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

/* <dialog> Section */
dialog {
  background-color: var(--light-grey);
  color: white;
  border-color: black;

  /* Centering a dialog section */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0px;
  padding: 2rem;
  border-radius: 8px;
  z-index: 1000;
}

dialog::backdrop {
  backdrop-filter: blur(1px);
}

.form-wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.task-notes-modal {
  outline: auto black;
  padding: 1rem;
  /* width: 100%; */
  overflow-wrap: break-word;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,0BAA0B;;AAG1B,+BAA+B;AAC/B;EACE,sBAAsB;EACtB,eAAe;AACjB;;AAEA;EACE,SAAS;EACT,UAAU;EACV,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,eAAe;EACf,YAAY;AACd;AACA,+BAA+B;;AAE/B,kBAAkB;;AAElB;EACE,4BAA4B;EAC5B,gCAAgC;EAChC,wBAAwB;EACxB,4BAA4B;AAC9B;;AAEA,kBAAkB;;AAElB;EACE,iCAAiC;EACjC,aAAa;EACb,6DAA6D;EAC7D,YAAY;AACd;;AAEA,6BAA6B;AAC7B;EACE,aAAa;EACb,aAAa;AACf;;AAEA,uBAAuB;;AAEvB,qBAAqB;AACrB;EACE,qCAAqC;EACrC,cAAc;EACd,iBAAiB;EACjB,iBAAiB;EACjB,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,aAAa;AACf;;AAEA,uBAAuB;AACvB;EACE,iBAAiB;AACnB;;AAEA,qBAAqB;AACrB;EACE,YAAY;EACZ,kCAAkC;EAClC,aAAa;EACb,0BAA0B;EAC1B,mCAAmC;EACnC,SAAS;AACX;;AAEA,0BAA0B;AAC1B;EACE,uBAAuB;EACvB,gBAAgB;EAChB,kBAAkB;EAClB,iBAAiB;EACjB,kBAAkB;EAClB,kBAAkB;EAClB,gBAAgB;EAChB,mCAAmC;EACnC,oBAAoB;;EAEpB,aAAa;EACb,sBAAsB;;EAEtB,8BAA8B;AAChC;;AAEA,2BAA2B;AAC3B;EACE,WAAW;EACX,aAAa;EACb,sBAAsB;EACtB,8BAA8B;EAC9B,8BAA8B;;EAE9B,kBAAkB;EAClB,oCAAoC;AACtC;;AAEA,4BAA4B;AAC5B;EACE,YAAY;EACZ,kCAAkC;AACpC;;AAEA,uBAAuB;AACvB;EACE,YAAY;EACZ,gBAAgB;EAChB,8BAA8B;EAC9B,eAAe;AACjB;;AAEA;EACE,sBAAsB;EACtB,cAAc;EACd,wBAAwB;EACxB,gBAAgB;AAClB;;AAEA;EACE,2EAA2H;EAC3H,wBAAwB;AAC1B;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,kBAAkB;EAClB,OAAO;EACP,MAAM;EACN,WAAW;EACX,YAAY;EACZ,UAAU;EACV,eAAe;EACf,sBAAsB;AACxB;AACA;EACE,kBAAkB;EAClB,OAAO;EACP,MAAM;EACN,WAAW;EACX,YAAY;EACZ,SAAS;EACT,UAAU;EACV,eAAe;AACjB;;AAEA,qBAAqB;AACrB;EACE,mCAAmC;EACnC,YAAY;EACZ,mBAAmB;;EAEnB,+BAA+B;EAC/B,eAAe;EACf,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,WAAW;EACX,aAAa;EACb,kBAAkB;EAClB,aAAa;AACf;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,aAAa;EACb,qCAAqC;EACrC,SAAS;AACX;;AAEA;EACE,mBAAmB;EACnB,aAAa;EACb,iBAAiB;EACjB,yBAAyB;AAC3B","sourcesContent":["/* CSS Fonts from Google */\n@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200..900;1,200..900&display=swap');\n\n/* CSS RESET BY DIGITAL